// Main

// Colors

$dark-shade: #1F2041;
$purple: #BC9CFF;
$green: #6FCF97;

$dark-shade-100: $dark-shade;
$dark-shade-75: transparentize($dark-shade, .25);
$dark-shade-50: transparentize($dark-shade, .5);
$dark-shade-25: transparentize($dark-shade, .75);
$dark-shade-5: transparentize($dark-shade, .95);

%h1 {
  font-size: 24px;
  line-height: 30px;
  font-weight: bold; }

%h2 {
  font-size: 19px;
  line-height: 23px;
  font-weight: bold; }

%h3 {
  font-size: 12px;
  line-height: 15px;
  font-weight: bold;
  text-transform: uppercase; }

%p {
  font-size: 14px;
  line-height: 24px;
  color: $dark-shade-75; }


// Colors & Types

%color-type__item-color {
  width: 70px;
  min-width: 50px;
  height: 70px;
  min-height: 50px;
  border-radius: 6px; }

// Form Elements

%input {
  display: block;
  box-sizing: border-box;
  min-width: 320px;
  height: 44px;
  border: 1px solid $dark-shade-25;
  border-radius: 4px;
  padding: 0 15px;
  margin-bottom: 20px;
  @extend %p;

  &::placeholder {
    color: $dark-shade-25; }

  &:hover, &:focus, &:active {
    border: 1px solid $dark-shade-50; } }
