@import '../vars';

.color-types {
  flex: 1 300px; }

.color-type__item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 19px;
  line-height: 28px;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0; }

  &_dark-shade-100 {
    @extend %color-type__item-color;
    background-color: $dark-shade-100; }

  &_dark-shade-75 {
    @extend %color-type__item-color;
    background-color: $dark-shade-75; }

  &_dark-shade-50 {
    @extend %color-type__item-color;
    background-color: $dark-shade-50; }


  &_dark-shade-25 {
    @extend %color-type__item-color;
    background-color: $dark-shade-25; }


  &_dark-shade-5 {
    @extend %color-type__item-color;
    background-color: $dark-shade-5; }


  &_purple {
    @extend %color-type__item-color;
    background-color: $purple; }


  &_green {
    @extend %color-type__item-color;
    background-color: $green; }

  &__color-name {
    margin-left: 40px;
    font-weight: bold;

    span {
      font-weight: normal; } } }
